router.get('/register-agent', (req, res) => {
	let newAgent = new Agent({
	username: 'diamond',
	password: '1234',
	createAt: moment()
	});
	newAgent.save().then(function(){
		console.log('agent acc saved')
		res.json({ accountCreated: true })
	})
})

export default router




